  --success-notify: rgba(35, 75, 35, .9);
  --info-notify: #3aa4e2;
  --modal-input-border-color: rgba(33, 33, 33, .2);
  --placeholder-text: rgba(117, 117, 117, .5);
  --auth-modal-background: #fcfbfa;
  --btn-up: rgba(255, 107, 8, .8);
  --btn-close: rgba(0, 0, 0, .5);
