  --scroll-background: #f6f8f4;
  --disabled: #989898;
  --transition-timing: cubic-bezier(.4, 0, .2, 1);
}

.pag__page {
  justify-content: center;
  align-items: center;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  height: 100%;
