  background: linear-gradient(30deg, var(--table-color) 40%, rgba(0, 0, 0, 0) 41%), linear-gradient(150deg, var(--table-color) 40%, rgba(0, 0, 0, 0) 41%), linear-gradient(270deg, var(--table-color) 30%, rgba(0, 0, 0, 0) 31%);
