.toggle__input {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  overflow: hidden;
}

.toggle__input:not([disabled]):active + .toggle-track, .toggle__input:not([disabled]):focus + .toggle-track {
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: 0 0 0 2px #373737;
}

.toggle__input:disabled + .toggle-track {
  cursor: not-allowed;
  opacity: .7;
}

.toggle-track {
  cursor: pointer;
  height: 26px;
  width: 52px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #908f8f;
  border-radius: 100px;
  display: flex;
  position: relative;
}

.toggle-track:hover, .toggle-track:focus {
  transition: box-shadow 200ms var(--transition-timing);
  box-shadow: 0 -4px 5px #fefefe, 0 -1px 1px #fefefe;
}

.toggle-indicator {
  height: 20px;
  width: 20px;
  background: #fff;
  border-radius: 20px;
  outline: 2px solid rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  transition: all .25s;
  display: flex;
  position: absolute;
  bottom: 2px;
  left: 2px;
  box-shadow: inset 5px -10px 15px rgba(0, 0, 0, .5);
}

.toggle__input:checked + .toggle-track .toggle-indicator {
  transform: translateX(26px);
}

.toggle__input:checked + .toggle-track .toggle-indicator .checkMark {
  opacity: 1;
  transition: opacity .25s ease-in-out;
}

.theme__icon {
